
import { defineComponent } from 'vue';
export default defineComponent({
  name: 'MusicPlayer',
  props: {
    id: String,
    src: String
  },
  data() {
    return {
      isPlaying: false
    };
  },
  methods: {
    play() {
      const audioElement: HTMLAudioElement = document.querySelector(
        `#${this.id}`
      ) as HTMLAudioElement;
      audioElement.play();
      this.isPlaying = true;
    },
    pause() {
      const audioElement: HTMLAudioElement = document.querySelector(
        `#${this.id}`
      ) as HTMLAudioElement;
      audioElement.pause();
      this.isPlaying = false;
    }
  }
});
